import React from 'react';
import { 
    AboutWrapper,
    Head,
    AboutImgWrapper,
    AboutImg,
    AboutTextWrapper,
    AboutTextContainer,
    AboutText,
    AboutTitle,
    AboutInfoTech
} from './About.elements';

const About = () => {
    return (
        <>
        <AboutWrapper>
            <AboutImgWrapper>
                <AboutImg src={require('../../images/about.jpg').default}/>
            </AboutImgWrapper>
            <AboutTextWrapper>
                <Head>ABOUT</Head>      
                <AboutTextContainer>
                    <AboutTitle>Motto</AboutTitle> 
                    <AboutText>
                        "클라이언트 개발에 필요한 기술이라면 무엇이든지!"
                    </AboutText>                   
                </AboutTextContainer>     

                <AboutTextContainer>
                    <AboutTitle>Motivation</AboutTitle> 
                    <AboutText>
                        우연히 친구의 부탁으로 AutoHotkey 매크로 프로그램을 제작하면서 개발의 즐거움을 알게 되었고 그 후 웹 프론트엔드 개발에 재미를 느껴 꾸준히 공부하면서 웹 어플리케이션 개발을 꿈꿔왔습니다. 
                    </AboutText>                   
                </AboutTextContainer>              
                
                <AboutTextContainer>
                    <AboutTitle>Skills</AboutTitle>
                    <AboutText>                                    
                        <AboutInfoTech>HTML5</AboutInfoTech>
                        <AboutInfoTech>CSS3</AboutInfoTech>
                        <AboutInfoTech>Javascript</AboutInfoTech>
                        <AboutInfoTech>React</AboutInfoTech>
                        <AboutInfoTech>Node.js</AboutInfoTech>
                        <AboutInfoTech>Express</AboutInfoTech>
                        <AboutInfoTech>Firebase</AboutInfoTech>                    
                    </AboutText>
                </AboutTextContainer>               
            </AboutTextWrapper>
        </AboutWrapper>   
        </>
    );
};

export default About;